<template>
  <div class="myPoints">
    <header>
      <div class="top">
        <div class="left">
          <p>零钱总额</p>
          <h1>2700.00</h1>
        </div>
        <div class="right" @click="$router.push({path:'/tixian'})">提现</div>
      </div>
      <div class="details">
        <p>结算中</p>
        <h2>9527.00</h2>
      </div>
    </header>
    <main>
      <h2>佣金明细</h2>
      <div class="item">
        <div class="left">
          <h3>9527</h3>
          <p>2019-02-14 11:09</p>
        </div>
        <div class="right">-9527</div>
      </div>
      <div class="item">
        <div class="left">
          <h3>9527</h3>
          <p>2019-02-14 11:09</p>
        </div>
        <div class="right green">+9527</div>
      </div>
    </main> 
  </div>
</template>

<script>
// import hello from "@/assets/images/bg@2x.png"
export default {
  data() {
    return {

      list: [],
      removePopup: false,
      buyList: []
    };
  },
  created() {
    // this.getData();
  },
  methods: {

  },
  filters: {
    formate: function(value) {
      let a = new Date(value);
      let b = a.toLocaleString();
      return b;
    }
  }
};
</script>


<style scoped lang="scss">
a {
  text-decoration: none;
}
.myPoints {
  padding: 0 25px;
  height:100vh;
  background: #FAFAFA;
  header {
    background: url('~@/assets/images/my/bg@2x.png') no-repeat;
    background-size: 100% 100%;
    height: 220px;
    color: #fff;
    padding: 40px;
    .top {
      display: flex;
      justify-content: space-between;
      align-items: center;
      .left {
        h1 {
          font-family: SourceHanSansCN-Regular;
          font-size: 45px;
          margin-bottom: 10px;
        }
        p {
          font-size: 13px;
        }
      }
      .right {
        padding:10px 40px;
        background: #fff;
        color:#00E5E4;
        border-radius: 30px;
        font-size: 22px;
      }
    }
    .details {
      margin:30px 0;
      .weihu {
        border-left: 1px solid rgba(255, 255, 255, 0.4);
        padding-left: 20px;
      }
    }
  }
  main{
    padding: 10px;
    h2{
      margin: 10px 0;
      font-size: 14px;
    }
    .item{
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-bottom:40px;
      padding-top:20px;
      border-bottom:2px dashed #eee;
      h2{
        font-size: 28px;
      }
      h3{
        font-size: 32px;
      }
      p{
        color:#A4A4A4;
        font-size: 22px;
      }
      .right{
        color:#ECAE33;
        font-size: 34px;
      }
      .green{
        color:#03D76C;
      }
    }
  }
}
</style>



